.sidebarmenu {
  z-index: 999;
  /* background-color: #15736e; */
  background-color: #141414;
  right: 0;
  top: 0;
  position: absolute;
  width: 25%;
  min-width: 250px;
  color: white;
  animation: slide-left 300ms ease-out forwards;
  box-shadow: -3px 0px 8px rgba(0, 0, 0, 0.55);
  padding: 25px;
  border-radius: 0px 0px 0px 60px;
}

@media (max-width: 1240px) {
  .sidebarmenu {
    min-width: 315px;
  }
}

.closeMenu {
  cursor: pointer;
}

.closeMenu:active svg {
  transform: scale(0.90);
  /* Scaling button to 0.98 to its original size */
  box-shadow: 3px 2px 22px 1px rgba(0, 0, 0, 0.24);
  /* Lowering the shadow */
}

.actions-list {
  list-style-type: none;
  padding: 0px;
  font-size: 20px;
}

.actions-list li {
  margin-top: 20px;
  margin-bottom: 20px;
  animation: margin-left 10s ease-in-out;
}

.actions-list li span {
  margin-right: 10px;
}

.actions-list li:hover {
  cursor: pointer;
  margin-left: 2px;
  text-decoration: underline;
}

@keyframes slide-left {
  from {
    opacity: 0;
    transform: translateX(0rem);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}
