.wrapper {
  width: 100%;
  margin-top: 25px;
  display: flex;
  justify-content: center;
}

.container {
  background-color: rgba(255, 255, 255, 0.5);
  width: calc(100vw - 25rem);
  left: calc(50% - 35rem);
  padding: 15px;
  border-radius: 30px;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 13px 27px -5px,
    rgba(0, 0, 0, 0.3) 0px 8px 16px -8px;
}

@media (max-width: 1145px) {
  .container {
    width: calc(100vw - 2rem);
  }
}

.cards {
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 2rem;
  padding: 0;
  list-style-type: none;

  display: flex;
  flex-flow: row wrap;
  align-content: space-between;
  justify-content: center;
}

.card {
  position: relative;
  display: block;
  height: 100%;
  border-radius: calc(15 * 1px);
  overflow: hidden;
  text-decoration: none;
  width: 160px;
  height: 220px;
  animation: all 0.8s ease-in-out;
}

@media (max-width: 769px) {
  .card  {
    width: 150px;
    height: 210px;
  }
}

.card:hover {
  box-shadow: rgba(0, 0, 0, 0.19) 0px 10px 20px, rgba(0, 0, 0, 0.23) 0px 6px 6px;
  transform: scale(1.01);
  cursor: pointer;
}

.card__image {
  width: 100%;
  height: auto;
}

.card__overlay {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 1;
  border-radius: calc(18 * 0.5px) calc(18 * 0.5px) 1px 1px;
  background-color: #fff;
  transform: translateY(100%);
  transition: 0.2s ease-in-out;
}

.card:hover .card__overlay {
  transform: translateY(0);
}

.card__header {
  position: relative;
  display: flex;
  align-items: center;
  gap: 10px;
  padding: 5px;
  border-radius: calc(15 * 0.5px) 0 0 0;
  background-color: #fff;
  transform: translateY(-100%);
  transition: 0.2s ease-in-out;
  text-align: center;
}

.card__header-text {
  width: 100%;
}

.card__arc {
  width: 80px;
  height: 80px;
  position: absolute;
  bottom: 98%;
  right: 0;
  z-index: 1;
}

.card__arc path {
  fill: #fff;
  d: path("M 40 80 c 22 0 40 -22 40 -40 v 40 Z");
}

.card:hover .card__header {
  transform: translateY(0);
}

.card__thumb {
  flex-shrink: 0;
  width: 50px;
  height: 50px;
  border-radius: 50%;
}

.card__title {
  font-size: 15px;
  padding: 1px;
  margin: 0px;
  /* color: #6a515e; */
}

.card__tagline {
  display: block;
  margin: 1em 0;
  font-family: "MockFlowFont";
  font-size: 0.8em;
  color: #d7bdca;
}

.card__mln-left .mln {
  font-size: 28px;
  font-weight: 600;
  color: #292929;
}

.card__fullname {
  font-size: 11px;
  text-align: center;
  color: rgb(110, 110, 110);
  margin: 0;
  margin-bottom: 3px;
}

.card__description {
  font-size: 12px;
  padding: 5px;
  margin: 0;
  color: #7a7a7a;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 3;
  overflow: hidden;
  text-align: center;
}

.sectionTitle {
  text-align: center;
  background: white;
  border-radius: 40px;
  padding-top: 5px;
  padding-bottom: 5px;
  text-align: center;
  margin: 15px;
}

.sectionTitle h2 {
  margin: 0px;
  text-transform: uppercase;
  letter-spacing: 2px;
}
