@import url("https://fonts.googleapis.com/css2?family=Montserrat&display=swap");

* {
  box-sizing: border-box;
}

html {
  font-family: "Montserrat", sans-serif;
}

body {
  margin: 0;
  color: #181818;
  /* background-color: #3f3f3f; */
  background-color: #505050;
  background-image: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)),
    url("./Assets/bg.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: 100%;
  background-attachment: fixed;
  background-color: black;
}

@media (max-width: 769px) {
  body p, h1, h2, h3, h4, h5, span, li {
    /* font-size: 95% !important; */
  }
}

.flex {
  display: flex;
}

button.primaryButton {
  font: inherit;
  cursor: pointer;
  background-color: transparent;
  border: 2px solid #1f1f1f;
  color: #1f1f1f;
  padding: 0.5rem 2rem;
  border-radius: 6px;
}

button.primaryButton.small {
  font: inherit;
  cursor: pointer;
  background-color: transparent;
  border: 2px solid #1f1f1f;
  color: #1f1f1f;
  padding: 2px 8px 1px 8px;
  border-radius: 6px;
}

button.primaryButton:hover,
button.primaryButton:active {
  background-color: #1f1f1f;
  border-color: #1f1f1f;
  color: white;
}

button.primaryButton:hover svg path {
  fill: white;
}

button.primaryButton:disabled,
button.primaryButton[disabled] {
  background-color: rgb(214, 214, 214);
  border: 1px solid rgb(214, 214, 214);
  cursor: auto;
  color: #848484;
}
button.primaryButton:disabled svg path,
button.primaryButton[disabled] svg path {
  fill: #848484;
}

button.primaryButton:disabled:hover svg path,
button.primaryButton[disabled]:hover svg path {
  fill: #848484;
}

/* button.primaryButton:disabled:hover,
button.primaryButton[disabled]:hover {
  color: #848484;
} */

button.danger {
  font: inherit;
  cursor: pointer;
  background-color: transparent;
  border: 2px solid #eb1717;
  color: #eb1717;
  padding: 0.5rem 2rem;
  border-radius: 6px;
}

button.danger:hover,
button.danger:active {
  background-color: #eb1717;
  border-color: #eb1717;
  color: white;
}

button.danger:hover svg g path {
  fill: #fff;
}

button:active {
  transform: scale(0.95);
  box-shadow: 3px 2px 22px 1px rgba(0, 0, 0, 0.24);
}

button.shuffleButton {
  font: inherit;
  cursor: pointer;
  background-color: white;
  border: 2px solid #1f1f1f;
  color: #1f1f1f;
  padding: 0.5rem 2rem;
  border-radius: 6px;
  display: flex;
  align-items: center;
}
button.shuffleButton:hover,
button.shuffleButton:active {
  background-color: #1f1f1f;
  border-color: #1f1f1f;
  color: white;
}

button.shuffleButton:hover svg path {
  fill: white;
}

/* Role Badges */
.roleBadge {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 25px;
  height: 25px;
  border-radius: 13px;
  margin-right: 4px;
  font-size: 12px;
}

.roleBadge.a,
.roleBadge.pc {
  background-color: #e21d1d;
  color: white;
}

.roleBadge.t,
.roleBadge.w {
  background-color: #b9009a;
  color: white;
}

.roleBadge.m,
.roleBadge.e,
.roleBadge.c {
  background-color: #00a0fd;
  color: white;
}

.roleBadge.ds,
.roleBadge.dc,
.roleBadge.dd,
.roleBadge.d {
  background-color: #00ce45;
  color: white;
}

.roleBadge.por,
.roleBadge.p {
  background-color: #e7d800;
  color: white;
}

.error {
  font-size: 14px;
  color: red;
  padding: 10px;
  margin: 0px;
  background-color: bisque;
  border-radius: 8px;
}
.success {
  font-size: 14px;
  color: rgb(0, 156, 60);
  padding: 10px;
  margin: 0px;
  background-color: rgb(174, 226, 178);
  border-radius: 8px;
}

.mobileReloader {
  position: fixed;
  background-color: red;
  z-index: 1;
  top: 90%;
  right: 8px;
  width: 45px;
  height: 45px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  border: none;
  cursor: pointer;
  z-index: 99999999999;
}

@media (min-width: 770px) {
  .mobileReloader {
    right: 20px;
  }
}

.MuiBox-root {
  padding: 0px !important;
  padding-left: 15px !important;
}
