.tableContainer {
  width: 90%;
}

@media (max-width: 700px) {
  .tableContainer {
    overflow-x: scroll;
  }
}

.infoContainer {
  width: 45%;
  padding: 10px;
}
.list {
  margin: 0;
  padding-left: 1px;
  list-style-type: none;
}

.list li {
  margin-top: 5px;
  margin-bottom: 5px;
}

.list li img {
  margin-right: 8px;
}

.list .listItem {
  margin-left: 5px;
  font-size: 18px;
  font-weight: 600;
}

.tableContainer table {
  width: 100%;
}

.tableContainer table {
  border-spacing: 0; /* Removes the cell spacing via CSS */
  border-collapse: collapse; /* Optional - if you don't want to have double border where cells touch */
}

.tableContainer table thead {
  background-color: #292929;
  color: white;
}

.tableContainer table thead tr th {
  padding: 6px;
}

.tableContainer table tbody tr td {
  padding: 5px;
}

.tableContainer table tbody tr:nth-child(even) {
  background-color: rgb(245, 245, 245);
}

.leftskipper {
  position: absolute;
  top: 50%;
  left: 0.5%;
  transform: scale(-1, 1);
  cursor: pointer;
}
.leftskipper:active {
  transform: scale(-1, 0.95);
}

.rightskipper {
  position: absolute;
  top: 50%;
  right: 0.5%;
  cursor: pointer;
}

.rightskipper:active {
  transform: scale(0.95);
}

.card {
  padding: 12px;
  width: 40%;
  box-shadow: rgba(0, 0, 0, 0.25) 2px 2px 2.8px;
  border-radius: 14px;
  border: 1px solid rgb(233, 233, 233);
  margin-bottom: 22px;
  margin-right: 22px;
}

.card:hover {
  box-shadow: rgba(0, 0, 0, 0.45) 4px 4px 3px;
}

div[role="tabpanel"] {
  width: 100%;
}

.flexWrapper {
  display: flex;
}

.w50 {
  width: 50%;
}

@media (max-width: 1000px) {
  .rightskipper svg {
    width: 22px;
  }
  .leftskipper svg {
    width: 22px;
  }

  div[class^="makeStyles-root"],
  div[class*="makeStyles-root"] {
    display: block !important;
    width: 88% !important;
    margin: auto !important;
  }
  .MuiTabs-vertical {
    min-width: 20%;
    width: 80%;
    margin: auto;
  }

  div[class^="jss"]:not(.MuiTabs-root),
  div[class*="jss"]:not(.MuiTabs-root) {
    display: block !important;
    width: 88% !important;
    margin: auto !important;
  }

  .MuiTabs-scroller {
    width: 100%;
  }

  div[class^="MuiBox-root"],
  div[class*="MuiBox-root"] {
    padding-left: 0 !important;
  }

  .tableContainer {
    width: 100%;
  }

  div[role="tabpanel"] {
    margin-top: 15px;
  }

  .flexWrapper {
    display: block;
  }

  .card {
    width: 100%;
    margin-right: 0px;
  }
  .w50 {
    width: 100%;
    min-height: 280px;
  }
}
