.fieldWrapper {
  padding-top: 10px;
  padding-bottom: 10px;
  margin-right: 20px;
}

.inp {
  position: relative;
  margin: auto;
  width: 100%;
  max-width: 280px;
  border-radius: 3px;
  overflow: hidden;
}
.inp .label {
  position: absolute;
  top: 0;
  left: 8px;
  font-size: 14px;
  color: rgba(0, 0, 0, 0.5);
  font-weight: 500;
  transform-origin: 0 0;
  transform: translate3d(0, 0, 0);
  transition: all 0.2s ease;
  pointer-events: none;
}
.inp .focus-bg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  transform: scaleX(0);
  transform-origin: left;
}
.inp input {
  -webkit-appearance: none;
  appearance: none;
  width: 100%;
  border: 0;
  font-family: inherit;
  height: 30px;
  font-size: 16px;
  font-weight: 400;
  box-shadow: inset 0 -1px 0 rgb(0 0 0 / 30%);
  transition: all 0.15s ease;
}
.inp input:hover {
  background: rgba(0, 0, 0, 0.04);
  /* box-shadow: inset 0 -1px 0 rgba(0, 0, 0, 0.5); */
}
.inp input:not(:-moz-placeholder-shown) + .label {
  color: rgba(0, 0, 0, 0.5);
  transform: translate3d(0, -12px, 0) scale(0.75);
}
.inp input:not(:-ms-input-placeholder) + .label {
  color: rgba(0, 0, 0, 0.5);
  transform: translate3d(0, -12px, 0) scale(0.75);
}
.inp input:not(:placeholder-shown) + .label {
  color: rgba(0, 0, 0, 0.5);
  transform: translate3d(0, -18px, 0) scale(0.75);
  left: 2px;
}
.inp input:focus {
  /* background: rgba(0, 0, 0, 0.05); */
  outline: none;
  box-shadow: inset 0 -2px 0 #161616;
  background: rgba(0, 0, 0, 0.05);
}
.inp input:focus + .label {
  color: rgba(0, 0, 0, 0.5);
  transform: translate3d(0, -18px, 0) scale(0.75);
  left: 2px;
}
.inp input:focus + .label + .focus-bg {
  transform: scaleX(1);
  transition: all 0.1s ease;
}

.flexWrapper {
  display: flex;
  align-items: center;
}
.flexWrapper2 {
  display: flex;
  align-items: center;
}

#max_price {
  margin-right: 20px;
}

.purchased {
  background-color: rgb(175, 175, 175);
  opacity: 0.3;
}

.removeItem {
  background: #cc3636;
  width: 30px;
  height: 30px;
  border-radius: 6px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
  cursor: pointer;
}

.removeItem:active {
  transform: scale(0.95);
  box-shadow: 3px 2px 22px 1px rgba(0, 0, 0, 0.24);
}

.slot {
  width: 25px;
  height: 25px;
}

.slot.primo {
  background-color: rgb(255, 0, 0);
}
.slot.secondo {
  background-color: rgb(255, 166, 0);
}
.slot.terzo {
  background-color: rgb(229, 255, 0);
}
.slot.quarto {
  background-color: rgb(66, 235, 0);
}
.slot.quinto {
  background-color: rgb(62, 181, 218);
}

.autocomplete {
  width: 50%;
  margin-right: 20px;
}

.tableContainer table {
  width: 100%;
}

.tableContainer table {
  border-spacing: 0; /* Removes the cell spacing via CSS */
  border-collapse: collapse; /* Optional - if you don't want to have double border where cells touch */
}

.tableContainer table thead {
  background-color: #292929;
  color: white;
}
.tableContainer .slotColumn {
  width: 5%;
}

.tableContainer table thead tr th {
  padding: 6px;
}

.tableContainer table tbody tr td {
  padding: 5px;
}

/* .tableContainer table tbody tr:nth-child(even) {
  background-color: rgb(245, 245, 245);
} */

.filtersWrapper {
  margin-bottom: 20px;
  display: flex;
}

.filterName {
  height: 28px;
  border-radius: 6px;
  border: 1px solid grey;
  padding-left: 10px;
}

.filterSlot {
  height: 28px;
  border-radius: 6px;
  border: 1px solid grey;
  cursor: pointer;
}

@media (max-width: 800px) {
  .flexWrapper {
    flex-direction: column;
  }
  .favouritePlayer {
    overflow-x: scroll;
  }

  .tableContainer {
    overflow-x: scroll;
  }

  .filtersWrapper {
    overflow-x: scroll;
  }

  .filtersWrapper {
    display: block;
  }

  .filterName {
    margin-bottom: 10px;
  }

  div[class^="PersonalArea_autocomplete"],
  div[class*="PersonalArea_autocomplete"] {
    width: 100%;
    margin-right: 0px;
    margin-bottom: 10px;
  }

  .fieldWrapper {
    width: 100%;
    margin: 0px;
    margin-bottom: 10px;
  }
}
