.wrapper {
  display: flex;
  justify-content: space-between;
}

.block {
  width: 30%;
  padding: 10px;
  border-radius: 10px;
  border: 2px solid #d6d6d6;
}

.block:hover {
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
}

.block h4 {
  margin-top: 0px;
}

.fieldWrapper {
  padding-top: 10px;
  padding-bottom: 10px;
  width: 100%;
}

.inp {
  position: relative;
  margin: auto;
  width: 100%;
  max-width: 280px;
  border-radius: 3px;
  overflow: hidden;
}
.inp .label {
  position: absolute;
  top: 0;
  left: 8px;
  font-size: 14px;
  color: rgba(0, 0, 0, 0.5);
  font-weight: 500;
  transform-origin: 0 0;
  transform: translate3d(0, 0, 0);
  transition: all 0.2s ease;
  pointer-events: none;
}
.inp .focus-bg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  transform: scaleX(0);
  transform-origin: left;
}
.inp input {
  -webkit-appearance: none;
  appearance: none;
  width: 100%;
  border: 0;
  font-family: inherit;
  height: 30px;
  font-size: 16px;
  font-weight: 400;
  box-shadow: inset 0 -1px 0 rgb(0 0 0 / 30%);
  transition: all 0.15s ease;
}
.inp input:hover {
  background: rgba(0, 0, 0, 0.04);
  /* box-shadow: inset 0 -1px 0 rgba(0, 0, 0, 0.5); */
}
.inp input:not(:-moz-placeholder-shown) + .label {
  color: rgba(0, 0, 0, 0.5);
  transform: translate3d(0, -12px, 0) scale(0.75);
}
.inp input:not(:-ms-input-placeholder) + .label {
  color: rgba(0, 0, 0, 0.5);
  transform: translate3d(0, -12px, 0) scale(0.75);
}
.inp input:not(:placeholder-shown) + .label {
  color: rgba(0, 0, 0, 0.5);
  transform: translate3d(0, -18px, 0) scale(0.75);
  left: 2px;
}
.inp input:focus {
  /* background: rgba(0, 0, 0, 0.05); */
  outline: none;
  box-shadow: inset 0 -2px 0 #161616;
  background: rgba(0, 0, 0, 0.05);
}
.inp input:focus + .label {
  color: rgba(0, 0, 0, 0.5);
  transform: translate3d(0, -18px, 0) scale(0.75);
  left: 2px;
}
.inp input:focus + .label + .focus-bg {
  transform: scaleX(1);
  transition: all 0.1s ease;
}

.MuiInputBase-root {
  width: 100%;
}

.custom-label {
  color: rgba(0, 0, 0, 0.5);
  display: block;
  margin-bottom: 2px;
  font-size: 11px;
}

.error {
  font-size: 14px;
  color: red;
  padding: 10px;
  margin: 0px;
  margin-top: 10px;
  background-color: bisque;
  border-radius: 8px;
}
.success {
  font-size: 14px;
  color: rgb(11, 100, 45);
  padding: 10px;
  margin: 0px;
  margin-top: 10px;
  background-color: rgb(174, 218, 178);
  border-radius: 8px;
}

@media (max-width: 1000px) {
  .wrapper {
    flex-direction: column;
  }
  .block {
    width: 100%;
    margin-bottom: 10px;
  }
}
