.wrapper {
  display: flex;
}

.usersList {
  width: 50%;
  margin: 10px;
}

.userBlock {
  display: flex;
  align-items: center;
  padding: 10px;
  margin-right: 40px;
  margin-top: 10px;
  margin-bottom: 10px;
  /* background-color: #f5f5f5; */
  border: 3px solid #d7d7d7;
  border-radius: 10px;
}
.userBlock:hover {
  box-shadow: rgba(0, 0, 0, 0.25) 2px 2px 2.8px;
}

.userImg {
  width: 130px;
  height: 100px;
  border-radius: 50%;
  margin-right: 15px;
  border: 1px solid rgb(228 228 228);
  background-size: cover;
  background-position: top;
  background-repeat: no-repeat;
}

.userData {
  padding-top: 10px;
  padding-bottom: 10px;
  width: 100%;
}

.inp {
  position: relative;
  margin: auto;
  width: 100%;
  max-width: 280px;
  border-radius: 3px;
  overflow: hidden;
}
.inp .label {
  position: absolute;
  top: 0;
  left: 8px;
  font-size: 14px;
  color: rgba(0, 0, 0, 0.5);
  font-weight: 500;
  transform-origin: 0 0;
  transform: translate3d(0, 0, 0);
  transition: all 0.2s ease;
  pointer-events: none;
}
.inp .focus-bg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  transform: scaleX(0);
  transform-origin: left;
}
.inp input {
  -webkit-appearance: none;
  appearance: none;
  width: 100%;
  border: 0;
  font-family: inherit;
  height: 30px;
  font-size: 16px;
  font-weight: 400;
  box-shadow: inset 0 -1px 0 rgb(0 0 0 / 30%);
  transition: all 0.15s ease;
}
.inp input:hover {
  background: rgba(0, 0, 0, 0.04);
  /* box-shadow: inset 0 -1px 0 rgba(0, 0, 0, 0.5); */
}
.inp input:not(:-moz-placeholder-shown) + .label {
  color: rgba(0, 0, 0, 0.5);
  transform: translate3d(0, -12px, 0) scale(0.75);
}
.inp input:not(:-ms-input-placeholder) + .label {
  color: rgba(0, 0, 0, 0.5);
  transform: translate3d(0, -12px, 0) scale(0.75);
}
.inp input:not(:placeholder-shown) + .label {
  color: rgba(0, 0, 0, 0.5);
  transform: translate3d(0, -18px, 0) scale(0.75);
  left: 2px;
}
.inp input:focus {
  /* background: rgba(0, 0, 0, 0.05); */
  outline: none;
  box-shadow: inset 0 -2px 0 #161616;
  background: rgba(0, 0, 0, 0.05);
}
.inp input:focus + .label {
  color: rgba(0, 0, 0, 0.5);
  transform: translate3d(0, -18px, 0) scale(0.75);
  left: 2px;
}
.inp input:focus + .label + .focus-bg {
  transform: scaleX(1);
  transition: all 0.1s ease;
}

.actions {
  margin-top: 10px;
}

.addUser {
  width: 50%;
}

.error {
  font-size: 14px;
  color: red;
  padding: 10px;
  margin: 0px;
  background-color: bisque;
  border-radius: 8px;
}

.deleteElement {
  position: relative;
  background-color: #e59a9a;
  /* padding: 8px; */
  border-radius: 50%;
  width: 40px;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid #f27d7d;
  cursor: pointer;
  top: 48px;
  left: -8px;
}

.deleteElement:hover svg {
  /* Start the shake animation and make the animation last for 0.5 seconds */
  animation: shake 0.5s;

  /* When the animation is finished, start again */
  animation-iteration-count: infinite;
}

@media (max-width: 1000px) {
  .wrapper {
    flex-direction: column;
  }
  .usersList {
    width: 100%;
    order: 2;
    margin: 0px;
    margin-top: 15px;
  }

  .addUser {
    width: 100%;
    order: 1;
  }

  .userBlock {
    margin-right: 0px;
  }

  .userImg {
    width: 140px;
  }
}

@keyframes shake {
  0% {
    transform: translate(1px, 1px) rotate(0deg);
  }
  10% {
    transform: translate(-1px, -2px) rotate(-1deg);
  }
  20% {
    transform: translate(-3px, 0px) rotate(1deg);
  }
  30% {
    transform: translate(3px, 2px) rotate(0deg);
  }
  40% {
    transform: translate(1px, -1px) rotate(1deg);
  }
  50% {
    transform: translate(-1px, 2px) rotate(-1deg);
  }
  60% {
    transform: translate(-3px, 1px) rotate(0deg);
  }
  70% {
    transform: translate(3px, 1px) rotate(-1deg);
  }
  80% {
    transform: translate(-1px, -1px) rotate(1deg);
  }
  90% {
    transform: translate(1px, 2px) rotate(0deg);
  }
  100% {
    transform: translate(1px, -2px) rotate(-1deg);
  }
}
