.teamImage {
  max-width: 70px;
  max-height: 70px;
  margin-right: 30px;
}

.title {
  font-size: 40px;
  margin: 0px;
  color: white;
}

.header {
  width: 100%;
  height: 100%;
  margin-right: 10px;
  display: flex;
  align-items: center;
  padding: 15px;
  border-radius: 8px 35px 8px 8px;
  margin-top: 10px;
  box-shadow: rgb(50 50 93 / 25%) 0px 2px 5px -1px,
    rgb(0 0 0 / 30%) 0px 1px 3px -1px;
}

.header.Spezia,
.header.Udinese,
.header.Juventus {
  background: linear-gradient(
    90deg,
    rgba(17, 17, 17, 1) 0%,
    rgba(255, 255, 255, 1) 100%
  );
}

.header.Atalanta,
.header.Inter {
  background: linear-gradient(
    90deg,
    rgba(0, 0, 0, 1) 0%,
    rgba(0, 30, 160, 1) 100%
  );
}

.header.Benevento,
.header.Salernitana,
.header.Torino {
  background: linear-gradient(
    90deg,
    rgba(94, 14, 17, 1) 0%,
    rgba(236, 236, 236, 1) 100%
  );
}

.header.Bologna,
.header.Cagliari,
.header.Genoa,
.header.Crotone {
  background: linear-gradient(
    90deg,
    rgba(26, 48, 72, 1) 0%,
    rgba(162, 28, 37, 1) 100%
  );
}

.header.Fiorentina {
  background: linear-gradient(
    90deg,
    rgba(97, 39, 160, 1) 0%,
    rgba(190, 130, 255, 1) 100%
  );
}

.header.Napoli,
.header.Lazio,
.header.Empoli {
  background: linear-gradient(
    90deg,
    rgba(18, 160, 214, 1) 0%,
    rgba(127, 204, 233, 1) 51%,
    rgba(167, 220, 240, 1) 77%,
    rgba(255, 255, 255, 1) 100%
  );
}

.header.Milan {
  background: linear-gradient(
    90deg,
    rgba(226, 34, 30, 1) 0%,
    rgba(0, 0, 0, 1) 100%
  );
}

.header.Venezia,
.header.Parma {
  background: linear-gradient(
    90deg,
    rgba(239, 127, 25, 1) 0%,
    rgba(133, 81, 33, 1) 25%,
    rgba(43, 42, 40, 1) 50%,
    rgba(42, 75, 43, 1) 75%,
    rgba(41, 93, 44, 1) 100%
  );
}

.header.Sassuolo {
  background: linear-gradient(
    90deg,
    rgba(0, 0, 0, 1) 0%,
    rgba(14, 166, 83, 1) 100%
  );
}

.header.Sampdoria {
  background: linear-gradient(
    90deg,
    rgba(0, 0, 254, 1) 0%,
    rgba(188, 188, 255, 1) 39%,
    rgba(255, 255, 255, 1) 61%,
    rgba(255, 111, 111, 1) 85%,
    rgba(255, 0, 0, 1) 100%
  );
}

.header.Verona {
  background: linear-gradient(
    90deg,
    rgba(255, 241, 0, 1) 0%,
    rgba(46, 49, 146, 1) 100%
  );
}

.header.Roma {
  background: linear-gradient(
    90deg,
    rgba(254, 180, 47, 1) 0%,
    rgba(158, 20, 43, 1) 100%
  );
}

.information {
  margin-top: 20px;
  padding: 10px;
  box-shadow: rgb(50 50 93 / 25%) 0px 2px 5px -1px,
    rgb(0 0 0 / 30%) 0px 1px 3px -1px;
  border-radius: 8px;
  width: 25%;
}

.rolesWrapper {
  display: flex;
  align-items: center;
}
.information div:not(:last-child) {
  margin-bottom: 12px;
}

.marketValue {
  font-size: 24px;
  font-weight: 600;
  margin-right: 2px;
  margin-left: 2px;
}
.marketCurrency {
  font-size: 10px;
  font-weight: 300;
}

.timerWrapper {
  margin-top: 20px;
  padding: 10px;
  box-shadow: rgb(50 50 93 / 25%) 0px 2px 5px -1px,
    rgb(0 0 0 / 30%) 0px 1px 3px -1px;
  border-radius: 8px;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100px;
  height: 100px;
  font-size: 40px;
  font-weight: 600;
  width: 25%;
}

.actions {
  width: 50%;
  margin-left: 20px;
  margin-top: 20px;
  padding: 10px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.currentValue {
  font-size: 60px;
  font-weight: 600;
}

.inputSelectorWrapper {
  display: flex;
  margin-top: 20px;
}

