.minus {
  cursor: pointer;
  width: 30px;
  background: #f2f2f2;
  border-radius: 4px;
  padding: 8px 5px 8px 5px;
  border: 1px solid #ddd;
  display: inline-block;
  margin-right: 5px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.plus {
  cursor: pointer;
  width: 30px;
  background: #f2f2f2;
  border-radius: 4px;
  padding: 8px 5px 8px 5px;
  border: 1px solid #ddd;
  display: inline-block;
  margin-left: 5px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.selector {
  height: 34px;
  width: 100px;
  text-align: center;
  font-size: 26px;
  border: 1px solid #ddd;
  border-radius: 4px;
  display: inline-block;
  width: 140px;
}

.buyerWrapper {
  display: flex;
  flex-direction: column;
  width: 240px;
  margin-top: 30px;
  text-align: center;
}

.custom-label {
  color: rgba(0, 0, 0, 0.5);
  display: block;
  margin-bottom: 2px;
  font-size: 11px;
}

.competitors {
  margin-top: 20px;
  padding: 10px;
  box-shadow: rgb(50 50 93 / 25%) 0px 2px 5px -1px,
    rgb(0 0 0 / 30%) 0px 1px 3px -1px;
  border-radius: 8px;
  width: 25%;
}

.competitors ul {
  list-style-type: none;
  padding-left: 5px;
}

.competitors ul li {
  margin-top: 10px;
  margin-bottom: 10px;
  display: flex;
  align-items: center;
}

.competitors ul li .indicator {
  width: 12px;
  height: 12px;
  margin-right: 7px;
  border-radius: 50%;
  border: 1px solid rgb(199, 199, 199);
}

.competitors ul li.active .indicator {
  background-color: #85ef85;
  animation: pulse 2s infinite;
}

.competitors ul li.not-active {
  color: #d1d1d1;
}
.competitors ul li.not-active .indicator {
  background-color: #d1d1d1;
}

@keyframes pulse {
  0% {
    -webkit-box-shadow: 0 0 0 0 rgba(120, 212, 120, 0.4);
  }
  70% {
    -webkit-box-shadow: 0 0 0 10px rgba(255, 0, 0, 0);
  }
  100% {
    -webkit-box-shadow: 0 0 0 0 rgba(255, 0, 0, 0);
  }
}

.modify-actions {
  width: 75%;
  margin-left: 20px;
  margin-top: 20px;
  padding: 10px;
}

.modifyPrice {
  border-radius: 25px 0px 2px 27px;
  border: 1px solid black;
  border-right: none;
  width: 75px;
  text-align: center;
  outline: none;
}

.modify-actions .modifyUser {
  width: 230px;
  padding-left: 5px;
  border-top: 1px solid black;
  border-right: 1px solid black;
  border-bottom: 1px solid black;
}

.modify-actions .MuiInputBase-root {
  width: 230px;
  padding-left: 5px;
  border-top: 1px solid black;
  border-right: 1px solid black;
}

.modify-actions .MuiInputBase-root:before {
  border-bottom: none;
}

.shuffleWrapper {
  padding: 5px;
  margin-top: 15px;
  display: flex;
  justify-content: flex-end;
}

.shuffleWrapper button {
  display: flex;
  align-items: center;
}

