canvas#wrapper {
  width: 37%;
  position: absolute;
  height: 87%;
  z-index: -1;
  margin: auto;
  left: 35%;
}

.flexWrapper {
  display: flex;
}

.modInfo {
  width: 75%;
  text-align: center;
}

@media (max-width: 1000px) {
  .flexWrapper {
    display: block;
  }

  .information {
    width: 100%;
  }

  .actions {
    width: 100%;
    margin-left: 0px;
  }

  .competitors {
    width: 100%;
  }

  .modify-actions {
    width: 100%;
    margin: 0px;
    margin-top: 10px;
  }

  .modify-actions img {
    margin: auto;
  }

  .modInfo {
    width: 100%;
  }

  canvas#wrapper {
    left: 25%;
    width: 50%;
    height: 92%;
  }

  .stickerWrapper {
    display: flex;
    justify-content: center;
    margin-top: 10px;
  }

  .stickerWrapper img {
    margin: 0px;
  }
}
