.wrapper {
  width: 100%;
  margin-top: 25px;
  display: flex;
  justify-content: center;
}

.container {
  background-color: rgba(255, 255, 255, 0.5);
  width: calc(100vw - 25rem);
  left: calc(50% - 35rem);
  padding: 15px;
  border-radius: 30px;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 13px 27px -5px,
    rgba(0, 0, 0, 0.3) 0px 8px 16px -8px;
}

@media (max-width: 1145px) {
  .container {
    width: calc(100vw - 2rem);
  }
}

.sectionTitle {
  text-align: center;
  background: white;
  border-radius: 40px;
  padding-top: 5px;
  padding-bottom: 5px;
  text-align: center;
  margin: 15px;
}

.sectionTitle h2 {
  margin: 0px;
  text-transform: uppercase;
  letter-spacing: 2px;
}

.bold {
  font-weight: 600;
}

.green {
  padding: 2px;
  padding-left: 10px;
  padding-right: 10px;
  background-color: #d4f8cd;
  border-radius: 6px;
  color: rgb(34, 158, 34);
}
