.header {
  /* position: sticky; */
  top: 0;
  left: 0;
  width: 100%;
  height: 5rem;
  /* background-color: #16aca5; */
  background-color: #1f1f1f;
  color: white;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 3%;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
  z-index: 10;
}

.gear {
  transition: 1.5s;
}

.gear:hover {
  cursor: pointer;
  -o-transform: rotate(720deg);
  -ms-transform: rotate(720deg);
  -moz-transform: rotate(720deg);
  -webkit-transform: rotate(720deg);
  transform: rotate(720deg);
}

.title {
  display: flex;
  align-items: center;
}

.title svg {
  margin-right: 10px;
  animation: bounce 1s infinite alternate;
  -webkit-animation: bounce .7s infinite alternate;
}

.badge {
  margin-right: 35px;
  padding: 8px;
  padding-left: 12px;
  padding-right: 12px;
  color: black;
  background-color: white;
  border-radius: 14px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 600;
}
.badge img {
  margin-right: 5px;
}

@keyframes bounce {
  from {
    transform: translateY(0px);
  }
  to {
    transform: translateY(-20px);
  }
}
@-webkit-keyframes bounce {
  from {
    transform: translateY(0px);
  }
  to {
    transform: translateY(-20px);
  }
}