/* Handle table */
.filterable-table-container {
  background-color: #ffffff;
  padding: 20px;
  border-radius: 20px;
}

@media (max-width: 769px) {
  .filterable-table-container {
    overflow-x: scroll;
  }
}

.pagination.pagination-sm.pull-right {
  display: none;
}

.table.table-condensed {
  width: 100%;
}

.filter-container {
  display: flex;
}

.filter-input {
  width: 200px;
  padding: 5px;
  padding-left: 10px;
  border: 2px solid #292929;
  border-radius: 14px;
  z-index: 2;
  background-color: white;
  outline: none;
  -webkit-appearance: none;
}

.close.clear-filter {
  font-size: 16px;
  color: white;
  padding: 2px;
  background: red;
  position: relative;
  left: -20px;
  padding-left: 24px;
  top: 0px;
  padding-right: 10px;
  z-index: 1;
  border-radius: 11px;
  border: 2px solid #292929;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
}
.close.clear-filter:hover {
  background: rgb(124, 3, 3);
}
.close.clear-filter:active {
  transform: scale(0.95);
}

select.form-control {
  padding: 5px;
  padding-left: 8px;
  padding-right: 8px;
  margin-top: 10px;
  border: 2px solid #292929;
  border-radius: 14px;
  cursor: pointer;
  outline: none;
}
.record-count {
  font-size: 12px;
  padding: 5px;
  color: rgb(110, 110, 110);
  margin-top: 5px;
}

table.table {
  border-spacing: 0; /* Removes the cell spacing via CSS */
  border-collapse: collapse; /* Optional - if you don't want to have double border where cells touch */
}

table.table th:first-child {
  border-radius: 10px 0 0 0;
}

table.table th:last-child {
  border-radius: 0 10px 0 0;
}

table.table thead tr {
  background-color: #292929;
  color: white;
}

table.table thead tr th {
  padding: 12px;
  text-align: left;
  cursor: pointer;
}

table.table tbody tr td {
  padding: 5px;
  padding-left: 10px;
  padding-right: 10px;
  cursor: pointer;
  min-width: 20%;
  font-size: 15px;
}

table.table tbody {
  border-bottom: 1px solid #292929;
}

table.table tbody tr:nth-child(even) {
  background-color: rgb(245, 245, 245);
}

table.table tbody tr:hover {
  background-color: rgb(230, 230, 230);
}

table.table tbody tr td:first-child {
  border-left: 1px solid #292929;
}

table.table tbody tr td:last-child {
  border-right: 1px solid #292929;
}

ul.pagination {
  display: flex;
  list-style-type: none;
  padding: 0;
}

ul.pagination li {
  margin-left: 5px;
  margin-right: 5px;
  border-radius: 4px;
  min-width: 30px;
  min-height: 30px;
  font-size: 12px;
  display: flex;
  justify-content: center;
  align-items: center;
}
ul.pagination li a {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 6px;
}

ul.pagination li.disabled {
  background-color: rgb(207, 207, 207);
  color: black;
}
ul.pagination li:not(.disabled) {
  background-color: #585858;
  color: white;
  cursor: pointer;
}
ul.pagination li.active {
  background-color: #292929;
}

.exact-filters {
  margin: 15px;
  font-size: 13px;
}

.filter-item-title {
  padding: 7px;
  padding-bottom: 8px;
  padding-left: 5px;
  border: 1px solid black;
  border-right: none;
  border-radius: 7px 0px 0px 7px;
}

.filter-item-value {
  padding: 7px;
  padding-bottom: 8px;
  border: 1px solid black;
  border-left: none;
  border-radius: 0px 7px 7px 0px;
  margin-right: 6px;
}

.filter-item-remove {
  cursor: pointer;
  padding-left: 8px;
  margin-right: 8px;
  padding-right: 8px;
  padding-bottom: 3px;
  color: white;
  background-color: rgb(209, 0, 0);
  margin-right: 6px;
  border-radius: 4px;
}

.filter-item-remove:after {
  content: "x";
}

.smallTeamLogo {
  max-width: 20px;
  max-height: 20px;
  margin-right: 5px;
}

.rolefilter-wrapper {
  display: flex;
}

@media (max-width: 1000px) {
  .col-sm-3.filter-container {
    display: block;
  }
  .rolefilter-wrapper {
    margin-top: 10px;
  }
}

.filterRoleBadge {
  width: 30px;
  min-width: 30px;
  height: 30px;
  border-radius: 0;
  margin-right: 10px;
  cursor: pointer;
  border-radius: 4px;
}

.filterRoleBadge.unactive {
  filter: alpha(opacity=40);
  /* IE */
  -moz-opacity: 0.4;
  /* Mozilla */
  opacity: 0.4;
  /* CSS3 */
}

.filterRoleBadge:active {
  transform: scale(0.95);
  box-shadow: 3px 2px 22px 1px rgba(0, 0, 0, 0.24);
}

ul.pagination li.btn-first-page {
  min-width: 45px;
}
ul.pagination li.btn-last-page {
  min-width: 45px;
}

table thead tr th {
  min-width: 140px;
}
